import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Img from 'gatsby-image';
import Link from 'gatsby-link';

const breakPoints = {
  sm: 320,
  md: 800,
  lg: 1200,
};

const Container = styled.div`
  background-color: #fff;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  box-shadow: 1px 2px 5px 0 rgba(52, 40, 22, 0.04), 0 1px 2px 0 rgba(53, 40, 22, 0.04);
  margin-bottom: 12px;
  @media screen and (min-width: ${breakPoints.md}px) {
    /*margin: 12px 6px;*/
    border-radius: 3px;
  }
`;
Container.displayName = 'Container';

const Frame = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 250px;
  padding: 20px 20px 16px 20px;
`;
Frame.displayName = 'Frame';

const Headline = styled.h2`
  font-family:  'Rubik', sans-serif;
  font-size: 33px;
  margin: 0 0 23px;
`;
Headline.displayName = 'Headline';

const Body = styled.p`
  font-family: 'Rubik', sans-serif;
  font-size: 1rem;
  margin: 0px;
`;
Body.displayName = 'Body';

const ButtonPanel = styled.div`
  font-family: 'Rubik', sans-serif;
  display: flex;
  padding: 16px 20px;
`;
ButtonPanel.displayName = 'ButtonPanel';

const Button = styled(Link)`
  padding: 12px 30px;
  border: 2px solid;
  border-color: ${props => props.background || ' #1A1819'};
  font-size: 16px;
  color: ${props => props.color || ' #1A1819'};
  border-radius: 3px;
  text-decoration: none;
  /*box-shadow: -3px 5px 0px  #1A1819;*/
  &:hover {
    /*
    color: ${props => props.colorHover || '#686868'};
    border-color: ${props => props.borderColorHover || '#686868'};
    */
    /*box-shadow: -1px 3px 0px  #1A1819; */
  }
  &:active {
    transform: scale(0.98);
    box-shadow: none;
  }
  &:focus {
    outline: none;
  }
`;
Button.displayName = 'Button';


function Card(props) {
  return (
    <Container>
      {props.imageSizes !== null && <Img sizes={props.imageSizes} />}
      <Frame>
        <Headline> {props.headline} </Headline>
        <Body>{props.body}</Body>
      </Frame>
      <ButtonPanel>
        <Button to={props.linkTo}> {props.buttonText} </Button>
      </ButtonPanel>
    </Container>
  );
}

Card.propTypes = {
  headline: PropTypes.string.isRequired,
  body: PropTypes.string.isRequired,
  imageSizes: PropTypes.objectOf(PropTypes.object),
  linkTo: PropTypes.string.isRequired,
  buttonText: PropTypes.string.isRequired,
};

Card.defaultProps = {
  imageSizes: null,
};

export default Card;



