import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Masonry from '../components/Masonry';
import Card from '../components/Card';
import Headline from '../components/Headline';
import Skills from '../components/Skills';
import Publications from '../components/Publications';

import PinkTop from '../images/top-angle--pink.svg';
import PinkBottom from '../images/bottom-angle--pink.svg';

import memphisPattern from '../images/memphis_pattern.svg';


// import Helmet from 'react-helmet';

// import '../css/index.css'; // add some style if you want!

const breakPoints = {
  sm: 320,
  md: 800,
  lg: 1200,
};


const Angle = styled.div`
  background-image: url(${props => props.image});
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-size: cover;
  background-position: center;
  height: 135px;
  margin-top: -2px;
  margin-bottom: -2px;
`;

const Memphis = styled.div`
  background-image: url(${memphisPattern});
  background-repeat: repeat;
  background-position: center;
  min-height: 220px;
  @media screen and (min-width: ${breakPoints.md}px) {
    min-height: 280px;
  }
  @media screen and (min-width: ${breakPoints.lg}px) {
    min-height: 340px;
  }
`;


export default function Index({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div
      style={{
        marginTop: '-145px',
      }}
    >
      <Angle image={PinkTop}>
        <Headline
          text="Projects"
          color="#dd7b7b"
          backgroundColor="#F6DDDD"
          boxShadowColor="#F1C9C9"
        />
      </Angle>
      <div
        style={{
          backgroundColor: '#FEF6F6',
          padding: '30px 0px',
        }}
      >
        <Masonry>
          {posts
            .filter(post => post.node.frontmatter.title.length > 0)
            .map(({ node: post }) => (
              <Card
                headline={post.frontmatter.title}
                body={post.excerpt}
                imageSizes={post.frontmatter.image.childImageSharp.sizes}
                linkTo={post.frontmatter.path}
                buttonText="View Project"
              />
            ))}
        </Masonry>
      </div>
      <Angle image={PinkBottom} />
      <Headline text="Skills" />
      <Skills />
      <Angle image={PinkTop}>
        <Headline
          text="Publications"
          color="#dd7b7b"
          backgroundColor="#F6DDDD"
          boxShadowColor="#F1C9C9"
        />
      </Angle>
      <div
        style={{
          backgroundColor: '#FEF6F6',
          padding: '30px 0px',
        }}
      >
        <Publications />
      </div>
      <Memphis>
        <Angle image={PinkBottom} />
      </Memphis>
    </div>
  );
}

Index.propTypes = {
  data: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 250)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image {
              childImageSharp {
                sizes(maxWidth: 630) {
                  ...GatsbyImageSharpSizes
                }
              }
            }
          }
        }
      }
    }
  }
`;



